import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  isLoading: false
};

const mutations = {
  changeLoadingTrue(state) {
    state.isLoading = true
  },
  changeLoadingFalse(state) {
    state.isLoading = false
  }
};

const actions = {
  changeLoadingTrueAction: function(ctx) {
    // 'changeLoadingTrue'mutationを実行
    ctx.commit('changeLoadingTrue')
  },
  changeLoadingFalseAction: function(ctx) {
    // 'changeLoadingFalse'mutationを実行
    ctx.commit('changeLoadingFalse')
  }
}

const store = new Vuex.Store({
  state,
  mutations,
  actions
});

export default store;