<template>
  <div id="app">
    <TopWrapper/>
    <BoxWrapper/>
    <div class="heading">
      <h1>よくあるご質問</h1>
    </div>
    <accordion>
      <div slot="title">他社のサービス・システムとの違いは何ですか？</div>
      <div class="content" slot="content">
        <p>KAKU Hybrid Cloudは、直感的に操作可能なシンプル画面設計と柔軟かつ一貫したプラットフォームを提供できることに加え、万全なサポート体制が整っていることが秀でている点です。特にサポート体制は、運営会社が多様なクラウドサービスを展開していることもあり豊富な知見を備えておりますので安心してシステムを導入頂けます。</p>
      </div>
    </accordion>
    <accordion>
      <div slot="title">初めての導入で不安ですがどんなサポートがありますか？</div>
      <div class="content" slot="content">
        <p>ございます。初期導入時は専任のサポートが担当させていただき、企業の運用・要件に合わせたご提案をさせていただきます。</p>
      </div>
    </accordion>
    <accordion>
      <div slot="title">利用料金を教えてください。</div>
      <div class="content" slot="content">
        <p>ご利用料金はシステム構築の規模によって変動します。お問い合わせにてご確認お願い致します。</p>
      </div>
    </accordion>
    <BottomWrapper/>
    <Footer/>
    <Loading :isLoading="isLoading"/>
  </div>
</template>

<script>
import TopWrapper from './components/TopWrapper.vue'
import BoxWrapper from './components/BoxWrapper.vue'
import Accordion from './components/Accordion.vue'
import BottomWrapper from './components/BottomWrapper.vue'
import Footer from './components/Footer.vue'
import Loading from './components/Loading.vue'

export default {
  name: 'App',
  components: {
    TopWrapper,
    BoxWrapper,
    Accordion,
    BottomWrapper,
    Footer,
    Loading,
  },
  created() {
      this.loadingTrue()
  },
  computed: {
    isLoading() {
      return this.$store.state.isLoading;
    }
  },
  methods: {
    loadingTrue() {
      this.$store.dispatch("changeLoadingTrueAction");
      this.timer();
    },
    loadingFalse() {
      this.$store.dispatch("changeLoadingFalseAction");
    },
    timer() {
      setTimeout(this.loadingFalse, 3000);
    }
  }
};
</script>

